@charset "UTF-8";

.black-theme {
    color: $black-theme-default-text-color;
    background: #feffff;

    a {
        color: $black-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#232526 0%,#37393a 45%, #ffffffb3 45%, #ffffffb3 100%);
    }
    
    .menu-title {
        color: #222426;
    }

    .written-date {
        color: #8e8e8e;
    }

    // tag
    .tag-item {
        background: #d5dcde;
        color: #5c6163;     
        
        &:before {
            border-right-color:#d5dcde;
        }
    }
        
}