@charset "UTF-8";

@font-face {
  font-family: "sang-sang-ant";
  src: url('fonts/SangSangAnt.otf') format('opentype');
}

body {
  height: 100vh;
}

main {
  padding-bottom: 3rem;
  min-height: 92%;
}

p {
  padding: 0;
  margin: 0;
}

hr {
  margin: 0;
  padding: 0;
  height: 1px;
  width: 100%;
  border-top: 1px solid rgba(0,0,0,.05);
}

ul {
  margin: 0;
  padding: 0;
}

a {
  &:hover {
    text-decoration: none;
  }
}

.pt-3 {
  padding-top: 3rem;
}

.pt-5 {
  padding-top: 5rem;
}

.pb-3 {
  padding-bottom: 3rem;
}

.pb-5 {
  padding-bottom: 5rem;
}

.mt-1 {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-3 {
  margin-top: 3rem;
}

.mt-4 {
  margin-top: 4rem;
}

.mt-5 {
  margin-top: 5rem;
}

.mt-6 {
  margin-top: 6rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mr-half {
  margin-right: .5rem;
}

.mr-1 {
  margin-right: 1rem;
}

.d-flex {
  display: flex;
}

.justify-content-center {
  justify-content: center;
}

.align-items-center {
  align-items: center;
}


// common
[class^=col] {
  padding: 0;
}

.site-visual {
  display: grid;
  justify-content: center;
  text-align: center;
  padding: 60px 10px 20px 10px;
  font-size: 35px;
   
  .avatar {
    border-radius: 50%;
    padding: 10px 5px;
    height: 10rem;
    background: #f5f5f5;
    box-shadow: 0px 4px 5px 1px #e2e2e2;
    -webkit-box-shadow: 0px 4px 5px 1px #e2e2e2;
    margin: 0 auto;
  }
}

.site-title {
  font-family: "sang-sang-ant";
  font-weight: bold;
}

.tag-container {
  list-style: none;
}

.tag-item {
  position: relative;
  display: inline-block;
  margin-right: 6px;
  padding: 1px 10px;
  font-size: 13px;
  border-radius: 4px;
  font-weight: 600;
  margin-bottom: 8px;
}

.empty {
  opacity: .7;
  text-align: center;
}