@charset "UTF-8";

.menu-container {
    list-style: none;
    text-align: center;
    background: #fffffff0;
    box-shadow: 1px 1px 1px 1px #33333308;
    position: sticky;
    top: 0;
    z-index: 999;
}

.menu-item {
    display: inline-block;
    padding: 1rem .8rem;
    font-weight: 550;
    opacity: 1;

    a {
        opacity: .6;
        padding: 2px 0;

        &:hover {
            opacity: 1;
            text-decoration: none;
        }
    }

    &.active {
        a {
            opacity: 1;
        }
    }
}