@charset "UTF-8";

.blue-theme {
    color: $blue-theme-default-text-color;
    background: #fdfeff;    

    a {
        color: $blue-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#86A8E7 0%,#a5c9e1 45%, #ffffffb3 45%, #ffffffb3 100%);
    }

    .menu-title {
        color: #7f80d5;
    }

    .written-date {
        color: #8e8e8e;
    }
    
    // tag
    .tag-item {
        background: #e1e1ea;
        color: #676c80;     
        border: 1px solid #cdcdd4;   
    }
}
