@charset "UTF-8";

.mango-theme {
    color: $mango-theme-default-text-color;
    background: #fffffd;

    a {
        color: $mango-theme-default-text-color;
    }

    .site-visual {
        background: linear-gradient(to bottom,#f09a1c 0%,#edd858 45%,rgba(255,255,255,0.701961) 45%,rgba(255,255,255,0.701961) 100%);
    }

    .menu-title {
        color: #e4a62b;
    }

    .written-date {
        color: #8e8e8e;
    }
    
    // tag
    .tag-item {
        background: #f1ede7;
        color: #8a8681;
        border: 1px solid #e2ddd5;
    }

    // post content 
    .post-content {
        color: #3c3835;

        strong {
            color: #e4a62b;
        }

        p {
            color: #56514d;
             
        }

        .date {
            color: #716c67;
        }
    }
}

