@charset "UTF-8";

.post-container {
    .empty {
        padding: 50px 10px;
        display: grid;
        
        &:before {
            content: "\f188";
            font: normal normal normal 14px/1 FontAwesome;
            font-size: 1.5rem;
            margin-bottom: 10px;
        }
    }
    
    .post-item {
        min-height: 12rem;
        margin: 0 1rem 2rem;

        &:not(:first-child) {
            &:before {
                content: '';
                border-top: 1px dashed #000;
                opacity: .1;
                width: 100%;
                margin-bottom: 2rem;
            }
        }
    }

    .post-item-header {
        display: flex;
        align-items: center;
        height: 8%;
    }

    .post-item-body {
        min-height: 81%;
    }

    .post-item-footer {
        height: 8%;
    }

    .title {
        line-height: 2;
        font-weight: bold;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .content-summary {
        opacity: .8;
        font-size: 1rem;
        overflow: hidden; 
        text-overflow: ellipsis; 
        display: -webkit-box; 
        -webkit-line-clamp: 3; 
        -webkit-box-orient: vertical; 
        word-wrap:break-word;
    }
    
    .menu-title {
        font-size: .9rem;
        font-weight: bold;
    }

    .written-date {
        font-size: .75rem;
        font-weight: 550;    
    }

    .thumbnail-wrapper {
        overflow: hidden;
        padding: 0;
        height: 12rem;
        border: 1px solid #c5c5c5;
        margin-bottom: 0;
        border-radius: 2px;
        margin-right: 1.5rem;
        
        a {
            &:after {
                content: '';
                background: #000;
                opacity: .1;    
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
            }

            &:hover {
                opacity: .9;
            }
        }
    }

    .thumbnail {
        width: 100%;
        height: 100%;
        object-fit: cover;        
    }

    @media (max-width: 768px) {
        .thumbnail-wrapper {
            margin-right: 0;
            margin-bottom: 1rem;
        }
    }
  
}