@charset "UTF-8";

.post-content {
    text-align: center;
    font-size: 16px;
    padding: 0 1.5rem;

    strong {
        font-size: 17px;
    }

    img {
        max-width: 100%;
        max-height: 500px;
        border: 1px solid #eeeeee;
    }

    p {
        line-height: 1.5;
        margin: 2rem 0;
    }

    .related-post-container {
        list-style: none;
        text-align: left;
    }

    .title {
        font-weight: bold;
    }

    .map {
        max-width: 100%;
        margin: 0 auto;
    }
    
    .image-title {
        margin-top: 4rem;
        font-size: 1rem;
        font-weight: 550;

        &:before {
            margin-right: .3rem;
            font: normal normal normal 14px/1 FontAwesome;
        }

        &.location:before {
            content: '\f041';
        }

        &.menu:before {
            content: '\f0c9';
        }
    }

    .date {
        margin: 0 0 2rem 0;
        font-size: 13px;
    }

    .related-item-title {
        border-bottom: 1px solid #00000014;
        padding-bottom: 12px;
        text-align: left;
        font-size: 1.1rem;
    }

    .tag-container {
        text-align: left;
    }

    .related-post-item {
        a {
            display: flex;
        }
        
        &:not(:first-child) {
            padding-top: 20px;
            border-top : 1px dotted #00000021;
        }
    
        .title {
            text-overflow: ellipsis;
            overflow: hidden;
            font-size: 17px;
        }

        .thumbnail {
            width: 100px;
            height: 80px;
            object-fit: cover;
        }

        .date {
            margin-bottom: 20px
        }
    }

    .usage-tip {
        border: 4px double #eeeeee;
        padding: 15px 30px;
        text-align: left;
        list-style: none;
        font-size: 14px;
        opacity: .85;
        font-weight: 550;
        display: inline-block;

        li {
            margin: 8px 0; 
            &:before {
                content: '\272D';
                margin-right: 8px;
            }
        }
    }
}